import React from 'react'
import { Button } from '@material-ui/core'
import { List, ListItem, ListItemText } from '@material-ui/core'
import db from './firebase'

const Todo = ({task}) => {

  const deleteMe = () => {
    db.collection('tasks').doc(task.id).delete();
  }
  return (
      <ListItem id='taskItem'>
        <ListItemText primary={task.task} secondary='dummy deadline'/>
        <Button color='primary' variant='contained' onClick={deleteMe}>Delete</Button>
      </ListItem>
  )
}

export default Todo
